import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { loadStripe } from "@stripe/stripe-js";
import {
  CardElement,
  Elements,
  useStripe,
  useElements,
} from "@stripe/react-stripe-js";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { updateFormData, submitOrder } from "../store/slices/checkoutSlice";
import { clearCart } from "../store/slices/cartSlice";
import { addOrder } from "../store/slices/ordersSlice";
import { formatPrice, calculateCartTotal, generateOrderId } from "../utils";
import { CheckoutFormData } from "../types";
import { Button } from "../components/Button";

const stripePromise = loadStripe(
  "pk_test_51SXbnX2E6FY4l7FJ5FF9gUaSFXz3psOfq6Z0gahLPOabu0VEWjzQYoW4a1Baw1IjEorRgOfjV7ZqFQ31xi6Crh9Y009m7JGKyU"
);

interface PaymentMethod {
  id: "card" | "paypal" | "apple_pay" | "google_pay" | "upi";
  name: string;
  icon: string;
}

const paymentMethods: PaymentMethod[] = [
  {
    id: "card",
    name: "Credit/Debit Card",
    icon: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT9MqnTgVTTvCYOT8UO_H6lkvA3nhyR33ScuQ&s",
  },
  {
    id: "paypal",
    name: "PayPal",
    icon: "data:image/png;base64,",
  },
  {
    id: "apple_pay",
    name: "Apple Pay",
    icon: "data:image/png;base64,",
  },
  {
    id: "google_pay",
    name: "Google Pay",
    icon: "data:image/png;base64,",
  },
  // {
  //   id: "upi",
  //   name: "UPI",
  //   icon: "https://upload.wikimedia.org/wikipedia/commons/thumb/0/0c/Unified_Payments_Interface_logo.svg/1200px-Unified_Payments_Interface_logo.svg.png",
  // },
];

const CheckoutForm: React.FC<{
  cartItems: any[];
  formData: Partial<CheckoutFormData>;
  errors: Partial<Record<keyof CheckoutFormData, string>>;
  onInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onAddressSelect: (addressId: string) => void;
  addresses: any[];
  onSubmit: (payload: {
    paymentMethod: "card" | "paypal" | "apple_pay" | "google_pay" | "upi";
    paymentToken?: string;
  }) => Promise<void> | void;
  selectedPayment: "card" | "paypal" | "apple_pay" | "google_pay" | "upi";
  onPaymentChange: (
    method: "card" | "paypal" | "apple_pay" | "google_pay" | "upi"
  ) => void;
  isProcessing: boolean;
}> = ({
  cartItems,
  formData,
  errors,
  onInputChange,
  onAddressSelect,
  addresses,
  onSubmit,
  selectedPayment,
  onPaymentChange,
  isProcessing,
}) => {
  const stripe = useStripe();
  const elements = useElements();
  const [useExistingAddress, setUseExistingAddress] = useState(false);
  const [selectedAddressId, setSelectedAddressId] = useState<string>("");
  const [paymentError, setPaymentError] = useState<string>("");

  const subtotal = calculateCartTotal(cartItems);
  const shipping = 15;
  const tax = subtotal * 0.1;
  const total = subtotal + shipping + tax;

  const defaultAddress = addresses.find((addr) => addr.isDefault);

  const handleAddressSelect = (addressId: string) => {
    const address = addresses.find((addr) => addr.id === addressId);
    if (address) {
      onAddressSelect(addressId);
      setSelectedAddressId(addressId);
    }
  };

  const handleFormSubmit = async (e: React.FormEvent) => {
    console.log("Submitting form");
    e.preventDefault();
    setPaymentError("");

    if (selectedPayment === "card") {
      if (!stripe || !elements) {
        setPaymentError("Stripe not loaded");
        return;
      }

      const cardElement = elements.getElement(CardElement);
      if (!cardElement) {
        setPaymentError("Card element not found");
        return;
      }

      try {
        const { token, error } = await stripe.createToken(cardElement);

        if (error) {
          setPaymentError(error.message || "Card validation failed");
          return;
        }

        // Call onSubmit with a payload (no event)
        await onSubmit({
          paymentMethod: "card",
          paymentToken: token?.id,
        });
      } catch (err) {
        setPaymentError("Payment processing failed");
      }
    } else {
      await onSubmit({
        paymentMethod: selectedPayment,
      });
    }
  };

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <h1 className="text-3xl font-bold mb-8">Checkout</h1>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2">
          <form onSubmit={handleFormSubmit} className="space-y-6">
            {/* Contact Information */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
              <h2 className="text-xl font-semibold mb-4">
                Contact Information
              </h2>
              <div className="space-y-4">
                <div>
                  <label
                    htmlFor="email"
                    className="block text-sm font-medium mb-1"
                  >
                    Email Address *
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.email
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.email && (
                    <p className="text-red-500 text-sm mt-1">{errors.email}</p>
                  )}
                </div>
              </div>
            </div>

            {/* Shipping Address */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-semibold">Shipping Address</h2>
                {addresses.length > 0 && (
                  <label className="flex items-center text-sm">
                    <input
                      type="checkbox"
                      checked={useExistingAddress}
                      onChange={(e) => {
                        setUseExistingAddress(e.target.checked);
                        if (e.target.checked && defaultAddress) {
                          handleAddressSelect(defaultAddress.id);
                        }
                      }}
                      className="mr-2 focus:ring-2 focus:ring-blue-500 rounded"
                    />
                    Use saved address
                  </label>
                )}
              </div>

              {useExistingAddress && addresses.length > 0 && (
                <div className="mb-4">
                  <label className="block text-sm font-medium mb-2">
                    Select Address
                  </label>
                  <select
                    value={selectedAddressId}
                    onChange={(e) => handleAddressSelect(e.target.value)}
                    className="w-full px-4 py-2 rounded-lg border border-gray-300 dark:border-gray-600 bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="">Choose an address</option>
                    {addresses.map((addr) => (
                      <option key={addr.id} value={addr.id}>
                        {addr.firstName} {addr.lastName} - {addr.address},{" "}
                        {addr.city}
                        {addr.isDefault ? " (Default)" : ""}
                      </option>
                    ))}
                  </select>
                </div>
              )}

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label
                    htmlFor="firstName"
                    className="block text-sm font-medium mb-1"
                  >
                    First Name *
                  </label>
                  <input
                    type="text"
                    id="firstName"
                    name="firstName"
                    value={formData.firstName || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.firstName
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.firstName && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors.firstName}
                    </p>
                  )}
                </div>

                <div>
                  <label
                    htmlFor="lastName"
                    className="block text-sm font-medium mb-1"
                  >
                    Last Name *
                  </label>
                  <input
                    type="text"
                    id="lastName"
                    name="lastName"
                    value={formData.lastName || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.lastName
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.lastName && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors.lastName}
                    </p>
                  )}
                </div>

                <div className="md:col-span-2">
                  <label
                    htmlFor="address"
                    className="block text-sm font-medium mb-1"
                  >
                    Address *
                  </label>
                  <input
                    type="text"
                    id="address"
                    name="address"
                    value={formData.address || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.address
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.address && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors.address}
                    </p>
                  )}
                </div>

                <div>
                  <label
                    htmlFor="city"
                    className="block text-sm font-medium mb-1"
                  >
                    City *
                  </label>
                  <input
                    type="text"
                    id="city"
                    name="city"
                    value={formData.city || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.city
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.city && (
                    <p className="text-red-500 text-sm mt-1">{errors.city}</p>
                  )}
                </div>

                <div>
                  <label
                    htmlFor="state"
                    className="block text-sm font-medium mb-1"
                  >
                    State *
                  </label>
                  <input
                    type="text"
                    id="state"
                    name="state"
                    value={formData.state || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.state
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.state && (
                    <p className="text-red-500 text-sm mt-1">{errors.state}</p>
                  )}
                </div>

                <div>
                  <label
                    htmlFor="zipCode"
                    className="block text-sm font-medium mb-1"
                  >
                    ZIP Code *
                  </label>
                  <input
                    type="text"
                    id="zipCode"
                    name="zipCode"
                    value={formData.zipCode || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.zipCode
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.zipCode && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors.zipCode}
                    </p>
                  )}
                </div>

                <div>
                  <label
                    htmlFor="country"
                    className="block text-sm font-medium mb-1"
                  >
                    Country *
                  </label>
                  <input
                    type="text"
                    id="country"
                    name="country"
                    value={formData.country || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.country
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.country && (
                    <p className="text-red-500 text-sm mt-1">
                      {errors.country}
                    </p>
                  )}
                </div>

                <div className="md:col-span-2">
                  <label
                    htmlFor="phone"
                    className="block text-sm font-medium mb-1"
                  >
                    Phone Number *
                  </label>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    value={formData.phone || ""}
                    onChange={onInputChange}
                    className={`w-full px-4 py-2 rounded-lg border ${
                      errors.phone
                        ? "border-red-500"
                        : "border-gray-300 dark:border-gray-600"
                    } bg-white dark:bg-gray-700 focus:ring-2 focus:ring-blue-500`}
                  />
                  {errors.phone && (
                    <p className="text-red-500 text-sm mt-1">{errors.phone}</p>
                  )}
                </div>
              </div>
            </div>

            {/* Payment Method Selection */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6">
              <h2 className="text-xl font-semibold mb-4">Payment Method</h2>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3 mb-6">
                {paymentMethods.map((method) => (
                  <button
                    key={method.id}
                    type="button"
                    onClick={() => onPaymentChange(method.id)}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      selectedPayment === method.id
                        ? "border-blue-500 bg-blue-50 dark:bg-blue-900/20"
                        : "border-gray-300 dark:border-gray-600 hover:border-gray-400"
                    }`}
                  >
                    <img
                      src={method.icon}
                      alt={method.name}
                      className="h-6 w-auto mx-auto"
                    />
                    <div className="text-sm font-medium text-center">
                      {method.name}
                    </div>
                  </button>
                ))}
              </div>

              {/* Card Payment Details */}
              {selectedPayment === "card" && (
                <div className="space-y-4">
                  <div className="p-4 rounded-lg border border-gray-300 dark:border-gray-600 bg-gray-50 dark:bg-gray-700">
                    <CardElement
                      options={{
                        style: {
                          base: {
                            fontSize: "16px",
                            color: "#424770",
                            "::placeholder": {
                              color: "#aab7c4",
                            },
                          },
                          invalid: {
                            color: "#9e2146",
                          },
                        },
                      }}
                    />
                  </div>
                  {paymentError && (
                    <p className="text-red-500 text-sm">{paymentError}</p>
                  )}
                </div>
              )}

              {/* Other Payment Methods Info */}
              {selectedPayment !== "card" && (
                <div className="p-4 rounded-lg bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800">
                  <p className="text-sm text-blue-900 dark:text-blue-200">
                    You will be redirected to{" "}
                    {paymentMethods.find((m) => m.id === selectedPayment)?.name}{" "}
                    to complete your payment securely.
                  </p>
                </div>
              )}
            </div>

            <Button
              type="submit"
              className="w-full"
              size="lg"
              disabled={isProcessing}
            >
              {isProcessing ? "Processing..." : "Place Order"}
            </Button>
          </form>
        </div>

        {/* Order Summary */}
        <div className="lg:col-span-1">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-6 sticky top-24">
            <h2 className="text-xl font-semibold mb-4">Order Summary</h2>

            <div className="space-y-3 mb-6">
              {cartItems.map((item) => (
                <div key={item.id} className="flex gap-3">
                  <img
                    src={item.product.images[0]}
                    alt={item.product.title}
                    className="w-16 h-16 object-cover rounded"
                  />
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-sm truncate">
                      {item.product.title}
                    </p>
                    <p className="text-sm text-gray-600 dark:text-gray-400">
                      Qty: {item.quantity}
                    </p>
                    <p className="text-sm font-semibold">
                      {formatPrice(item.product.price * item.quantity)}
                    </p>
                  </div>
                </div>
              ))}
            </div>

            <div className="border-t dark:border-gray-700 pt-4 space-y-2">
              <div className="flex justify-between text-sm">
                <span>Subtotal</span>
                <span>{formatPrice(subtotal)}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span>Shipping</span>
                <span>{formatPrice(shipping)}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span>Tax</span>
                <span>{formatPrice(tax)}</span>
              </div>
              <div className="border-t dark:border-gray-700 pt-2 flex justify-between font-bold text-lg">
                <span>Total</span>
                <span>{formatPrice(subtotal + shipping + tax)}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export const Checkout: React.FC = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const cartItems = useAppSelector((state) => state.cart.items);
  const formData = useAppSelector((state) => state.checkout.formData);
  const addresses = useAppSelector((state) => state.address.addresses);

  const [form, setForm] = useState<Partial<CheckoutFormData>>(formData);
  const [errors, setErrors] = useState<
    Partial<Record<keyof CheckoutFormData, string>>
  >({});
  const [selectedPayment, setSelectedPayment] = useState<
    "card" | "paypal" | "apple_pay" | "google_pay" | "upi"
  >("card");
  const [isProcessing, setIsProcessing] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
    setErrors((prev) => ({ ...prev, [name]: "" }));
  };

  const handleAddressSelect = (addressId: string) => {
    const address = addresses.find((addr) => addr.id === addressId);
    if (address) {
      setForm({
        ...form,
        firstName: address.firstName,
        lastName: address.lastName,
        address: address.address,
        city: address.city,
        state: address.state,
        zipCode: address.zipCode,
        country: address.country,
        phone: address.phone,
      });
    }
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<Record<keyof CheckoutFormData, string>> = {};

    if (!form.email) newErrors.email = "Email is required";
    else if (!/\S+@\S+\.\S+/.test(form.email))
      newErrors.email = "Invalid email";

    if (!form.firstName) newErrors.firstName = "First name is required";
    if (!form.lastName) newErrors.lastName = "Last name is required";
    if (!form.address) newErrors.address = "Address is required";
    if (!form.city) newErrors.city = "City is required";
    if (!form.state) newErrors.state = "State is required";
    if (!form.zipCode) newErrors.zipCode = "ZIP code is required";
    if (!form.country) newErrors.country = "Country is required";
    if (!form.phone) newErrors.phone = "Phone is required";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (payload: {
    paymentMethod: "card" | "paypal" | "apple_pay" | "google_pay" | "upi";
    paymentToken?: string;
  }) => {
    if (!validateForm()) return;

    if (cartItems.length === 0) {
      alert("Your cart is empty");
      return;
    }

    setIsProcessing(true);

    try {
      const subtotal = calculateCartTotal(cartItems);
      const shipping = 15;
      const tax = subtotal * 0.1;
      const total = subtotal + shipping + tax;

      const order = {
        id: generateOrderId(),
        items: cartItems,
        total,
        checkoutData: form as CheckoutFormData,
        paymentMethod: payload.paymentMethod,
        paymentToken: payload.paymentToken || "",
        orderDate: new Date().toISOString(),
      };

      dispatch(updateFormData(form));
      dispatch(submitOrder(order));
      dispatch(addOrder(order));
      dispatch(clearCart());
      navigate("/success");
    } catch (err) {
      console.error("Order submission failed:", err);
    } finally {
      setIsProcessing(false);
    }
  };

  if (cartItems.length === 0) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-16 text-center">
        <svg
          className="w-24 h-24 mx-auto text-gray-400 mb-4"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={1.5}
            d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
          />
        </svg>
        <h2 className="text-2xl font-bold mb-4">Your cart is empty</h2>
        <p className="text-gray-600 dark:text-gray-400 mb-6">
          Add some products before checking out
        </p>
        <Button onClick={() => navigate("/")}>Browse Products</Button>
      </div>
    );
  }

  return (
    <Elements stripe={stripePromise}>
      <CheckoutForm
        cartItems={cartItems}
        formData={form}
        errors={errors}
        onInputChange={handleInputChange}
        onAddressSelect={handleAddressSelect}
        addresses={addresses}
        onSubmit={handleSubmit}
        selectedPayment={selectedPayment}
        onPaymentChange={setSelectedPayment}
        isProcessing={isProcessing}
      />
    </Elements>
  );
};
